import React, {Component} from 'react';
import Navbar from 'react-bootstrap/Navbar'
import {Nav} from "react-bootstrap";

const NAV_INITIAL = 'navbar';

class Navigation extends Component {

    constructor(props) {
        super(props);
        this.state = {navClass: NAV_INITIAL}
    }

    componentDidMount() {
        window.addEventListener('scroll', this.handleScroll);
    }

    componentWillUnmount() {
        window.removeEventListener('scroll', this.handleScroll);
    }

    handleScroll = () => {
        const shrinkNav = document.body.scrollTop > 100 || document.documentElement.scrollTop > 100;
        let navClass = NAV_INITIAL;
        if (shrinkNav) {
            navClass += ' navbar-shrink';
        }
        this.setState({navClass});
    }

    render() {

        return (
            <Navbar bg="secondary" variant="dark" expand="lg" fixed="top">
                <div className="container">

                    <Navbar.Brand href="#home">
                        <img className="nav-image" src={process.env.PUBLIC_URL + '/img/navbar.png'} alt=""/>
                    </Navbar.Brand>
                    <Navbar.Toggle aria-controls="basic-navbar-nav"/>
                    <Navbar.Collapse id="basic-navbar-nav">
                        <Nav className="ml-auto">
                            <Nav.Link href="#portfolio">Portfolio</Nav.Link>
                            <Nav.Link href="#skills">Skills</Nav.Link>
                        </Nav>
                    </Navbar.Collapse>
                </div>
            </Navbar>
        );
    }

}

export default Navigation;
