import {OverlayTrigger, Tooltip} from "react-bootstrap";


const PortfolioItem = (props) => {
    const rowClass = (props.id % 2) === 1 ? 'row no-gutters row-polygon' : 'row no-gutters';
    const imgCol = (props.id % 2) === 1 ? 'col-lg-6 order-lg-last order-sm-first text-center' : 'col-lg-6 text-center';
    const txtCol = (props.id % 2) === 1 ? 'col-lg-6 p-2' : 'col-lg-6 p-2';

    return (
        <div className={rowClass}>
            <div className="col-lg-12">
                <div className="row no-gutters">
                    <div className={imgCol}>
                        <img className="portfolio-img" src={process.env.PUBLIC_URL + props.imgUrl} alt=""/>
                    </div>
                    <div className={txtCol}>
                        <h4><a href={props.url}>{props.name}</a></h4>
                        <p style={{whiteSpace: 'pre-line'}}>
                            {props.description}
                        </p>
                    </div>
                </div>

                <div className="row no-gutters">
                    <div className="col-12 mt-5 text-center">
                        {props.stack.map((url, key) =>
                            <OverlayTrigger
                                overlay={
                                    <Tooltip id={`tooltip`}>
                                        {url.replaceAll('_', ' ')}
                                    </Tooltip>
                                }
                                key={key}>
                                <img alt={url} className="stack-logos mb-2 mr-3"
                                     src={process.env.PUBLIC_URL + '/img/logos/' + url + '.png'}/>
                            </OverlayTrigger>
                        )}
                    </div>
                </div>
            </div>
        </div>
    )
}

export default PortfolioItem;
