import PortfolioItem from "./PortifolioItem";
import items from '../resources/portfolio.json'

const Portfolio = () => {

    return (
        <section className="page-section portfolio container-fluid" id="portfolio">
            <h2 className="page-section-heading text-center text-uppercase text-secondary mb-0">Portfolio</h2>
            <div className="divider-custom">
                <div className="divider-custom-line"/>
                <div className="divider-custom-icon"><i className="fas fa-star"/></div>
                <div className="divider-custom-line"/>
            </div>
            {items.map((item, key) => <PortfolioItem {...item} id={key} key={key}/> )}
        </section>
    );
}

export default Portfolio;
